.header{
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: 1px solid black;
}

.nav-items > ul{
   list-style-type: none;
   display: flex;
}

.nav-items>ul>li{
    padding: 10px;
}

.Logo{
    width: 100px;
}

.jsx{
    background-color: azure;
}

.card{
    width: 250px;
    border: 1px solid black;
    padding: 10px;
    margin: 10px
}

.card>img{
    width: 100%;
}

.Restaurant-List{
    display: flex;
    flex-wrap:wrap;
}

